import { reproducirSonidoBotones } from "../Enumeraciones/exportFunction";
import { Imagenes, Variables, AtlasImagenes, AtlasImagenesDetalles } from "./../Enumeraciones/Diccionario";

export class EstrellaSeleccionar {
	private escena: Phaser.Scene;
	private botonMas: Phaser.GameObjects.Image;
	private botonMenos: Phaser.GameObjects.Image;
	private estrella: Phaser.GameObjects.Image;
	// private bola: Phaser.GameObjects.Sprite;
	// private textoBola: Phaser.GameObjects.Text;
	private grupoNumeros: Phaser.GameObjects.Group;

	constructor(escene: Phaser.Scene) {
		this.escena = escene;
		this.crear();
		this.posicionarNumeroSeleccionado();
	}

	private crear() {

		const callbackCambiarNumero = (incrementar: boolean) => {

			let numeroEstrella = <number> this.escena.registry.get(Variables.EstrellaSeleccionada);
			numeroEstrella += (incrementar ? 1 : -1);
			if (numeroEstrella <= 0) {
				numeroEstrella = 15;
			} else if (numeroEstrella >= 16) {
				numeroEstrella = 1;
			}
			this.escena.registry.set(Variables.EstrellaSeleccionada, numeroEstrella);
			this.posicionarNumeroSeleccionado();
		};

		this.botonMas = this.escena.add.image(800, 200, Imagenes.SeleccionMas)
			.setDepth(1)
			.setScale(0.5)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_MOVE, function () {
				this.setTint(0x00ff00);
			})
			.on(Phaser.Input.Events.POINTER_OUT, function () {
				this.clearTint();
			})
			.on(Phaser.Input.Events.POINTER_UP, () => {
				reproducirSonidoBotones(this.escena);
				callbackCambiarNumero(true);
			});

		this.botonMenos = this.escena.add.image(800, 400, Imagenes.SeleccionMenos)
			.setDepth(1)
			.setScale(0.5)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_MOVE, function () {
				this.setTint(0x00ff00);
			})
			.on(Phaser.Input.Events.POINTER_OUT, function () {
				this.clearTint();
			})
			.on(Phaser.Input.Events.POINTER_UP, () => {
				reproducirSonidoBotones(this.escena);
				callbackCambiarNumero(false);
			});

		this.estrella = this.escena.add.image(800, 300, Imagenes.Estrella).setScale(0.5);

		// this.grupoNumeros = this.escena.add.group(null, {
		// 	key: AtlasImagenes.Numeros,
		// 	repeat: 1,
		// 	frame: AtlasImagenesDetalles[AtlasImagenes.Numeros].callbackObtenerFrame(5),
		// 	setXY: {
		// 		x: 800,
		// 		y: 250,
		// 		stepX: 50
		// 	},
		// });
		const decena = this.escena.add.sprite(794, 300, AtlasImagenes.Numeros, AtlasImagenesDetalles[AtlasImagenes.Numeros].callbackObtenerFrame(1)).setDisplaySize(12, 19);
		const unidad = this.escena.add.sprite(806, 300, AtlasImagenes.Numeros, AtlasImagenesDetalles[AtlasImagenes.Numeros].callbackObtenerFrame(5)).setDisplaySize(12, 19);

		this.grupoNumeros = this.escena.add.group();
		this.grupoNumeros.add(decena);
		this.grupoNumeros.add(unidad);
	}

	private posicionarNumeroSeleccionado() {
		const numeroEstrella = <number> this.escena.registry.get(Variables.EstrellaSeleccionada);
		const listaHijos = this.grupoNumeros.getChildren();
		if (numeroEstrella < 10) {
			(<Phaser.GameObjects.Sprite> listaHijos[0]).visible = false;
			(<Phaser.GameObjects.Sprite> listaHijos[1]).setPosition(800, 300);
			(<Phaser.GameObjects.Sprite> listaHijos[1]).setFrame(AtlasImagenesDetalles[AtlasImagenes.Numeros].callbackObtenerFrame(numeroEstrella));
		} else {
			(<Phaser.GameObjects.Sprite> listaHijos[0]).visible = true;
			(<Phaser.GameObjects.Sprite> listaHijos[1]).setPosition(806, 300);
			(<Phaser.GameObjects.Sprite> listaHijos[1]).setFrame(AtlasImagenesDetalles[AtlasImagenes.Numeros].callbackObtenerFrame(numeroEstrella - 10));
		}
	}
}
