import { Imagenes, Sonidos, Variables } from "./../Enumeraciones/Diccionario";

export default class MenuScene extends Phaser.Scene {
	public static Name = "MenuScene";
	private botonConfiguracion: Phaser.GameObjects.Image;
	private imagenMenu: Phaser.GameObjects.Image;
	private botonMusica: Phaser.GameObjects.Image;
	private botonMusicaEfectos: Phaser.GameObjects.Image;
	private botonHome: Phaser.GameObjects.Image;
	private cerrandoEscena = false;
	public init() {
		// por implementar
		// console.log("----------------------------------");
		// console.log(this.cerrandoEscena);
		// console.log("----------------------------------");
		this.cerrandoEscena = false;
	}

	public preload(): void {
		// por implementar

	}

	public create(): void {
		const texturaFondo = this.make.renderTexture({ width: this.cameras.main.displayWidth, height: this.cameras.main.displayHeight }, false);
		texturaFondo.fill(0xffffff, 0.2, 0, 0, this.cameras.main.displayWidth, this.cameras.main.displayHeight);
		texturaFondo.saveTexture('texturaFondo');
		this.add.image(0, 0, "texturaFondo")
			.setOrigin(0, 0)
			.setInteractive()
			.on(Phaser.Input.Events.POINTER_UP, () => {
				if (!this.cerrandoEscena) {
					this.cerrarEscena();
				}
			});

		this.crearBotonConfig();
		this.imagenMenu = this.add.image(0, 0, Imagenes.MenuBarra)
			.setDepth(1)
			.setScale(0.5);
		this.crearBotonMusica();
		this.crearBotonMusicaEfectos();
		this.crearBotonHome();
		const container = this.add.container(0, -100);
		container.add([this.imagenMenu, this.botonMusica, this.botonMusicaEfectos, this.botonHome]);
		this.add.tween({
			targets: [container],
			y: 0,
			ease: 'Cubic', //'Cubic', 'Elastic', 'Bounce', 'Back'
		});
	}

	crearBotonConfig() {
		this.botonConfiguracion = this.add.image(30, 30, Imagenes.MenuConfiguracion)
			.setScale(0.5)
			.setDepth(2)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_UP, () => {
				if (!this.cerrandoEscena) {
					this.cerrarEscena();
				}
			})
			.on(Phaser.Input.Events.POINTER_MOVE, function () {
				this.setTint(0x00ff00);
			})
			.on(Phaser.Input.Events.POINTER_OUT, function () {
				this.clearTint();
			});
	}

	crearBotonMusica() {
		const fnSonidoActivo = () => {
			const sonido = this.sound.get(Sonidos.MusicaFondo);
			return sonido?.isPlaying;
		};
		this.botonMusica = this.add.image(30, 140, Imagenes.Musica)
			.setDepth(2)
			.setScale(0.5)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_MOVE, () => {
				this.botonMusica.setTint(0x00ff00);
			})
			.on(Phaser.Input.Events.POINTER_OUT, () => {
				if (fnSonidoActivo()) {
					this.botonMusica.clearTint();
				} else {
					this.botonMusica.setTint(0xff504d);
				}
			});

		this.botonMusica.on(Phaser.Input.Events.POINTER_UP, () => {
			const sonido = this.sound.get(Sonidos.MusicaFondo);
			if (sonido?.isPlaying) {
				sonido.stop();
			} else {
				sonido.play({loop: true});
			}
		});
	}

	crearBotonMusicaEfectos() {
		const efectosSonido = <boolean> this.registry.get(Variables.MusicaEfectosActivada);
		this.botonMusicaEfectos = this.add.image(102, 100, efectosSonido ? Imagenes.MusicaEfectos : Imagenes.MusicaEfectosMute)
			.setDepth(2)
			.setScale(0.5)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_UP, () => {
				const efectosSonidoActivados = <boolean> this.registry.get(Variables.MusicaEfectosActivada);
				this.registry.set(Variables.MusicaEfectosActivada, !efectosSonidoActivados);

				if (!efectosSonidoActivados) {
					this.botonMusicaEfectos.clearTint();
					this.botonMusicaEfectos.setTexture(Imagenes.MusicaEfectos);
				} else {
					this.botonMusicaEfectos.setTint(0xff504d);
					this.botonMusicaEfectos.setTexture(Imagenes.MusicaEfectosMute);
				}
			});

		if (!efectosSonido) {
			this.botonMusicaEfectos.setTint(0xff504d);
		}
		// this.botonMusicaEfectos.on(Phaser.Input.Events.POINTER_UP, () => {
		// 	this.EfectosSonidoActivados = !this.EfectosSonidoActivados;
		// 	if (this.EfectosSonidoActivados) {
		// 		this.botonMusicaEfectos.clearTint();
		// 		this.botonMusicaEfectos.setTexture('MusicaEfectos');
		// 	} else {
		// 		this.botonMusicaEfectos.setTint(0xff504d);
		// 		this.botonMusicaEfectos.setTexture('MusicaEfectosMute');
		// 	}
		// });

		this.botonMusicaEfectos.on(Phaser.Input.Events.POINTER_MOVE, function () {
			this.setTint(0x00ff00);
		});
		this.botonMusicaEfectos.on(Phaser.Input.Events.POINTER_OUT, () => {
			// const efectosSonido = <boolean> this.registry.get(Variables.MusicaEfectosActivada);
			if (<boolean> this.registry.get(Variables.MusicaEfectosActivada)) {
				this.botonMusicaEfectos.clearTint();
			} else {
				this.botonMusicaEfectos.setTint(0xff504d);
			}
		});
	}

	crearBotonHome() {
		this.botonHome = this.add.image(140, 32, Imagenes.Home)
			.setDepth(2)
			.setScale(0.5)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_UP, () => {
				this.registry.events.emit('evt_MenuAbrirAcercaDe');
			})
			.on(Phaser.Input.Events.POINTER_MOVE, function () {
				this.setTint(0x00ff00);
			})
			.on(Phaser.Input.Events.POINTER_OUT, function () {
				this.clearTint();
			});
	}

	cerrarEscena() {
		this.cerrandoEscena = true;
		const container = this.add.container(0, 0);
		container.add([this.imagenMenu, this.botonMusica, this.botonMusicaEfectos, this.botonHome]);
		this.add.tween({
			targets: [container],
			y: -300,
			ease: 'Cubic', //'Cubic', 'Elastic', 'Bounce', 'Back'
		})
			.on('complete', () => {
				this.scene.resume(this.registry.get(Variables.EscenaLaunchMenu));
				this.scene.stop(MenuScene.Name);
			});
	}
}
