	crearBotonConfig() {
		this.botonConfiguracion = this.add.image(30, 30, Imagenes.MenuConfiguracion)
			.setScale(0.5)
			.setDepth(2)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_UP, () => {
				this.registry.set(Variables.EscenaLaunchMenu, SeleccionEscene.Name);
				this.scene.launch('MenuScene');
				this.scene.pause(SeleccionEscene.Name);
			})
			.on(Phaser.Input.Events.POINTER_MOVE, function () {
				this.setTint(0x00ff00);
			})
			.on(Phaser.Input.Events.POINTER_OUT, function () {
				this.clearTint();
			});
	}

	crearBotonSeleccionAzar() {
		this.botonSeleccionAzar = this.add.image(300, 500, Imagenes.BotonGrande)
			.setScale(0.5)
			.setDepth(2)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_UP, () => {
				reproducirSonidoBotones(this);
				const listaNumeros = numerosAzar();
				this.registry.set(Variables.NumerosSeleccionados, listaNumeros);
				this.listaNumerosSeleccionados.forEach(element => {
					element.refrescar();
				});
			})
			.on(Phaser.Input.Events.POINTER_MOVE, function () {
				this.setTint(0x00ff00);
			})
			.on(Phaser.Input.Events.POINTER_OUT, function () {
				this.clearTint();
			});

		this.add.text(300, 485, 'Aleatorio')
			.setOrigin(0.5, 0)
			.setFontFamily("monospace")
			.setFontSize(26)
			.setFill("#fff");
	}

	crearBotonComenzar() {
		this.botonSeleccionAzar = this.add.image(1000, 300, Imagenes.Comenzar)
			.setScale(0.5)
			.setDepth(2)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_UP, () => {
				reproducirSonidoCambioEscena(this);
				this.cameras.main.fadeOut(500, 0, 0, 0);
				this.cameras.main.once(Phaser.Cameras.Scene2D.Events.FADE_OUT_COMPLETE, (cam, effect) => {
					this.scene.start(MainGame.Name);
				});
			})
			.on(Phaser.Input.Events.POINTER_MOVE, function () {
				this.setTint(0x00ff00);
			})
			.on(Phaser.Input.Events.POINTER_OUT, function () {
				this.clearTint();
			});

		this.add.text(1000, 210, 'Comenzar')
			.setOrigin(0.5, 0)
			.setFontFamily("monospace")
			.setFontSize(26)
			.setFill("#fff");
	}

